import React from 'react'
import './Work.css'

function WorkA({lang, theme}) {
  return (
    <div className={theme ? "light" : "dark"}>
    <div className="container-fluid work">
      <div className="row d-flex justify-content-center">
        <div className="col-lg-11 col-xl-9 ">
          <div className="row">
            <div className="col-lg-12  d-flex justify-content-center">
              <video autoPlay loop control={"true"} className="w-100">
                <source
                  src="/mg-full/Work(A)/Fit2one_Video_Reel.mp4"
                  type="video/mp4"
                />
              </video>
            </div>
            <div className="col-lg-12 d-flex justify-content-center  work">
              <div className="row">
                <div className="col-lg-12 d-flex justify-content-center ">
                  {" "}
                  <h6>Web Design - Wordpress - Video Animation</h6>
                </div>
                <div className="col-lg-12 d-flex justify-content-center ">
                  {" "}
                  <h1>Fit2one</h1>
                </div>
                <div className="col-lg-12 d-flex justify-content-center ">
                  <img
                    src="mg-full/Work(A)/fit2one-smartphones.png"
                    width="100%"
                    alt="workA"
                  />
                </div>
                <div className="col-lg-12 d-flex justify-content-center overflow-x">
                  <div className=''>
                  <img
                    src="mg-full/Work(A)/fit2one-screenshot1.png"
                    alt="workA"
                    className="work-screenshot"
                  />
                  <img
                    src="mg-full/Work(A)/fit2one-screenshot2.png"
                    alt="workA"
                    className="work-screenshot"
                  />
                  <img
                    src="mg-full/Work(A)/fit2one-screenshot3.png"
                    alt="workA"
                    className="work-screenshot"
                  />
                  <img
                    src="mg-full/Work(A)/fit2one-screenshot4.png"
                    alt="workA"
                    className="work-screenshot"
                  />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
}

export default WorkA