import React from 'react'

function WorkB({ lang, theme }) {
  return (
    <div className={theme ? "light" : "dark"}>
      <div className="container-fluid work">
        <div className="row d-flex justify-content-center">
          <div className="col-lg-11 col-xl-9 ">
            <div className="row">
              <div className="col-lg-12  d-flex justify-content-center">
                <video autoPlay loop control={"true"} className="w-100">
                  <source
                    src="/mg-full/Work(B)/Taladros.mp4"
                    type="video/mp4"
                  />
                </video>
              </div>
              <div className="col-lg-12 d-flex justify-content-center  work">
                <div className="row">
                  <div className="col-lg-12 d-flex justify-content-center ">
                    {" "}
                    <h6>Web Design - Wordpress - Video Animation</h6>
                  </div>
                  <div className="col-lg-12 d-flex justify-content-center ">
                    {" "}
                    <h1>TALADROS</h1>
                  </div>
                  <div className="col-lg-12 d-flex justify-content-center ">
                    <img
                      src="mg-full/Work(B)/taladrosdebateria-smartphones.png"
                      width="100%"
                      alt="workA"
                    />
                  </div>
                  <div className="col-lg-12 d-flex justify-content-center  overflow-x">
                    <div className=''>
                    <img
                      src="mg-full/Work(B)/taladrosdebateria-screenshot1.png"
                      alt="workA"
                      className="work-screenshot"
                    />
                    <img
                      src="mg-full/Work(B)/taladrosdebateria-screenshot2.png"
                      alt="workA"
                      className="work-screenshot"
                    />
                    <img
                      src="mg-full/Work(B)/taladrosdebateria-screenshot3.png"
                      alt="workA"
                      className="work-screenshot"
                    />
                    <img
                      src="mg-full/Work(B)/taladrosdebateria-screenshot4.png"
                      alt="workA"
                      className="work-screenshot"
                    />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default WorkB