import React,{useEffect} from 'react'
import WorkA from './WorkA/WorkA'

import WorkB from './WorkB/WorkB'


function Work({lang,theme}) {
   useEffect(() => {
    window.scrollTo(0, 0)
  },[])
  return (
    <div>
        <WorkA lang={lang} theme={theme}/>
        <WorkB lang={lang} theme={theme}/>
    </div>
  )
}

export default Work